.App {
  min-height: 100%;
  display: flex;
  color: white;
}

.sideBar {
  background-color: #1b1b1d;
  height: 100vh;
  width: 244px;
  flex: 0 0 244px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transform: translateX(0);
  position: fixed; /* Add this to make the sidebar fixed */
  z-index: 1; /* Add this to make sure the sidebar is behind the main content */
}

.mainBar {
  height: 100vh;
  width: calc(
    100% - 244px
  ); /* Adjust width to take into account the sidebar width */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  flex-grow: 1;
  margin-left: 244px; /* Add this to provide space for the sidebar */
}

.burgerButton {
  display: none;
  cursor: pointer;
  font-size: 24px;
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 2;
}

@media only screen and (max-width: 767px) {
  .sideBar {
    transform: translateX(-100%);
  }

  .burgerButton {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px;
  }

  .sideBar.visible {
    transform: translateX(0);
  }

  .mainBar {
    width: 100%; /* Adjust width for smaller screens */
    margin-left: 0; /* Remove the margin for smaller screens */
  }
}
